.header {
  width: 100%;
  max-width: 1440px;
}

.nav {
  display: flex;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1 1;
}

.logo__img {
  width: 44px;
  height: 44px;
}

.logo__text {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 100%;
  letter-spacing: 0px;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.nav__link {
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  color: #88888D;
}
.nav__link:hover {
  color: #FFFFFF;
}

.nav__link--template {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 20px;
}
.nav__link--template:Hover {
  color: inherit;
}